import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Settings from '../views/Settings.vue'
import Layout from '../components/Layout.vue'
import Descriptive from '../views/Descriptive.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '',
    component: Layout,
    children:
    [
      {
        path: '/',
        name: 'Home',
        component: Home
      },
      {
        path: '/settings',
        name: 'Settings',
        component: Settings
      },
      {
        path: '/descriptive/:id',
        name: 'Descriptive',
        component: Descriptive
      },
    ]
  }
]

const router = new VueRouter({
  //mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
