<template>
    <div class="header">
        <div class="head">
            <img src="@/assets/cepal.png" alt="" @click="$router.push({name:'Home'})">
            <img src="@/assets/dnp.jpg" alt="" @click="$router.push({name:'Home'})">
            <div></div>
            <b-button variant="outline-primary" @click="$router.push({name: 'Settings'})"> <i class="fas fa-cog"></i> {{$t('header.config')}}</b-button>
        </div>
        <div class="sub-head">
            <div class="searcher">
                <div class="search">
                    <vue-bootstrap-typeahead
                        id="geo"
                        :data="data"
                        v-model="search"
                        ref="taSearch"
                        class="grid-j"
                        :serializer="s => s['CODIGO'] + ' - ' + s['DIVI_POL'] + '/' + s['SUB_DIVI_POL']"
                        :placeholder="$t('header.search')"
                        :value="searcht"
                        @hit="searcht = $event"
                    ></vue-bootstrap-typeahead>
                    <b-button variant="outline-primary"> <i class="fas fa-search"></i> {{$t('header.start')}}</b-button>
                </div>
                <div class="message" v-if="searchData['ID_TERRITORIO'] == undefined">
                    {{$t('header.message')}}
                </div>
                <div class="message" v-else>
                    {{searchData['CODIGO']}} - {{searchData['PAIS'].toUpperCase()}} - {{searchData['DIVI_POL']}} - {{searchData['SUB_DIVI_POL']}}
                </div>
            </div>
            <div>

            </div>
        </div>
    </div>
</template>

<script>
import _ from 'lodash'
export default {
    data(){
        return{
            search: '',
            data: [],
            searcht: {},
            searchData: {}
        }
    },
    watch: {
        search: _.debounce(function(search) { 
            if(search.length > 0){
               this.findData(search.toUpperCase() )
            }
            else{
                this.searcht = {}
            }
            this.$refs['taSearch'].$data.inputValue  = search.toUpperCase() 
        }, 500),
        searcht : function(newValue, oldValue){
            newValue['ID_TERRITORIO'] != undefined ? this.goToDescriptive(): null
        },
    },
    methods:{
        findData(search){
            eel.find_territory(search)((val) => {
                this.data = JSON.parse(val)
            })
        },
        goToDescriptive(){
            this.$router.push({name:'Descriptive', params:{id: this.searcht['ID_TERRITORIO']}})
            this.searchData = this.searcht
            this.searcht = {}
            this.$refs['taSearch'].$data.inputValue = ""
            this.search = ''
        }
    }
}
</script>

<style  scoped>
.header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    
    
}
.header>.head{
    display: grid;
    grid-template-columns: 1fr 1fr 5fr 3fr;
    align-items: center;
    justify-items: center;
    height: 110px;
    border-bottom: 1px solid #007bff;
    background-color: #f7f7f7;
}
.header>.head>img{
    height: 100px;
    cursor: pointer;
}
.header>.sub-head{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 15px;
    margin: 5px;
    height: 110px;  
}
.header .searcher{
    border-radius: 5px; 
    padding: 10px;
    background-color: #f7f7f7;
    border: 1px solid #007bff;
}
.searcher .message{
    margin: 5px;
    text-align: center;
    font-weight: bold;
    font-size: 16pt;
}
.header>.sub-head .search{
    display: grid;
    grid-template-columns: 4fr 1fr;
    gap: 15px;
}
</style>